.tb_post_cont_in__001.decorateTxt p::after,
.tb_post_cont_in__001.decorateTxt p::before {

    font-family: taggboxsocialfonts;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.tb_post_cont_in__001.decorateTxt p::before {
    content: "\f10d";
    margin: 0 10px 0 0;
    text-align: left;
    position: relative;
    top: -5px;
}

.tb_post_cont_in__001.decorateTxt p::after {
    content: "\f10e";
    margin: 5px 0 0 10px;
    text-align: right;
    position: relative;
    top: 5px;
}




.tb_wall_wrap__ .swiper-container {
